import React, { Component } from 'react';
import './skill.css';
import Picdummy from '../about-me/img/square-image.png';
import code from './img/coding.png';
import sr from '../scrollReveal.js'

class Skill extends Component {

    constructor(props){
        super(props);
        this.state = {
          skill_use_programing: ['PHP','Java','.NET','Javascript','SQL'] ,
          skill_lea_programing: ['React','Angular','NodeJS'],
          use_design: ['HTML','CSS','Jquery','Ajax','Bootstrap','UX','Semantic-UI'],
          skill_git:['Git','SourceTree'],
          skill_other:['AWS/EC2', 'MVC', 'OOP', 'CI Framework' , 'MongoDB']

          // set value  ที่ App.js <Learncompo name='ame form App.js' 
          // เวลาเรียนตอนแรก this.props.name => this.state.name
         /* <table>
                            <tr>
                                <td class='top'><a class="ui green label">Used</a></td>
                                <td class='text-start'>{use_webdesign}</td>
                            </tr>
                        </table> */
        };
      }

      componentDidMount = () => {
        let config = { 
            duration: 2000,
            origin: 'right',
            distance: '100px',
            viewFactor:0.2
        } 
        sr.reveal('#skill', config)

        config = { 
            duration: 2000,
            delay: 500,
        } 
        sr.reveal('#skillblock', config, 200)
      }
      
    
  
  render() {

    //<i class="fa fa-code-fork circle-skill"></i>
    //<div class="ui center aligned container h1skill"><h1>My Skill</h1></div>  
    //<i class="fa fa-code circle-skill"></i>
    //<p class ="center">Used: PHP , Java , .NET , Javascript , SQL </p>
    //<p class ="center">Leaning : React , Angular , Node.js </p>
    let use_program = this.state.skill_use_programing.map(
        (el, index) => {
          return <a class="ui green small basic label margin-lebel">{el}</a>
        }
    );
    let lea_program = this.state.skill_lea_programing.map(
        (el, index) => {
            return <a class="ui blue small basic label ">{el}</a>
        }
    ); 
    let use_webdesign = this.state.use_design.map(
        (el, index) => {
            return <a class="ui green small basic label ">{el}</a>
        }
    ); 
    let git = this.state.skill_git.map(
        (el, index) => {
            return <a class="ui green small basic label ">{el}</a>
        }
    );
    let other = this.state.skill_other.map(
        (el, index) => {
            return <a class="ui green small basic label ">{el}</a>
        }
    );
    
    return (
        
     <div class="center background-skill padding-end">
        <div id='skill' class="ui  bar-right" ref='skillhead'><h1 class="inline">My</h1> <h1 class="inline paint-word" >Skill</h1></div>
         <div class="ui center aligned container " >
            <div class="ui stackable three column grid " id='skillblock'>
                <div class="column " id='skillblock'>
                    <img class="webdesign circle-skill" src="data:image/svg+xml;utf8;base64," />
                    <div class="border-skill">
                        <div>
                        <h3>Programming Language</h3>
                        <p class ="center">Used: PHP , Java , .NET , Javascript , SQL </p>
                        <p class ="center">Learning : React , Angular , Node.js </p>
                       </div>
                    </div>
                </div>
                <div class="column" id='skillblock'>
                    <img class="webdesign circle-skill" src="data:image/svg+xml;utf8;base64," />
                    <div class="border-skill" >
                        <div>
                        <h3>Web Design</h3>
                        <p class ="center">Used: HTML, CSS, Jquery, Ajax, Bootstrap, Semantic-UI </p>
                        </div>
                    </div>
                </div>
                <div class="column" id='skillblock'>
                    <i class="fa fa-code-fork circle-skill"></i>
                    <div class="border-skill">
                        <div>
                        <h3>Version Control</h3>
                        <p class ="center"> Git, SourceTree</p>
                        </div>
                    </div>
                </div>
                <div class="column" id='skillblock'>
                <img class="webdesign circle-skill" src="data:image/svg+xml;utf8;base64," />
                    <div class="border-skill">
                        <div>
                        <h3>Other</h3>
                        <p class ="center"> AWS/EC2, MVC, OOP, CI Framework , MongoDB</p>
                        </div>
                    </div>
                </div>

                <div class="column" id='skillblock'>
                    
                    <img class="glasses circle-skill"src="data:image/svg+xml;utf8;base64," />
                    
                    <div class="border-skill">
                        <div>
                        <h3>Glasses</h3>
                        <p class ="center">Glasses is accessory me using everyday !</p>
                        <p class ="left"></p>
                        </div>
                    </div>
                </div>
                
                <div class="column" id='skillblock'>
                    <img class="webdesign circle-skill" src="data:image/svg+xml;utf8;base64," />
                    <div class="border-skill">
                        <div>
                        <h3>Game</h3>
                        <p class ="center">I love games. I is real gamer. </p>
                        <p class ="center">CS:GO, PUBG, ROV </p>
                        </div>
                    </div>
                </div>
                
                
 
            </div>  
         </div>
     </div>
    
    );
  }
}

export default Skill;



