import React, { Component } from 'react';
import './contact.css'
import sr from '../scrollReveal.js'

class Contact extends Component {

    componentDidMount = () => {
        let config = {
            
            duration: 2000,
            origin: 'top',
            distance: '30px',
            rotate: { x: 100, y: 100, z: 150 },
            viewFactor:0.2
        } 
        sr.reveal('.heart.icon', config)

        config = {
            delay: 1000,
            duration: 2000,
            origin: 'bottom',
            distance: '30px',
            rotate: { x: 0, y: 0, z: 0 },
            viewFactor:0.2
        } 
        sr.reveal('#text-bye', config)
    }

  render() {

    
    //<div class="ui segment-aboutme bar-right"><h1 class="inline contact-header">Contact </h1> </div>
    /*     
        <p>Your can contact by 
        <b>  E-mail <i class="mail icon"></i> : chayut.dai@gmail.com | </b>
        <b> Telephone <i class="text telephone icon"></i> :  081-485-9666 | </b> 
        <b> <i class="github icon"></i> :   </b>
        </p>
    */
        
    return (
       
        <div>
            <div class='ui center aligned container padding-head'>
                <div>
                    <h2 class="ui icon header">
                        <i class="heart icon"></i>
                       <h2 id="text-bye"> Thank you for visit.</h2>
                    </h2>

                    
                </div>
            </div>
            <div class='padding-footer end-footer'>

                <div class="ui center aligned container ">
                    <div class="ui stackable three column grid ">
                        <div class="column">
                            <i class="fa fa-envelope circle-contact"></i>
                            <div class="border-skill">
                                <div>
                                <h3>Contact by mail </h3>
                                <p>chayut.dai@gmail.com</p>
                                </div>
                            </div>
                        </div>
                        <div class="column">
                            <i class="fa fa-github circle-contact"></i>
                            <div class="border-skill">
                                <div>
                                <h3>Contact by github </h3>
                                <p>https://github.com/ChayutDai/</p>
                                </div>
                            </div>
                        </div>
                        <div class="column">
                            <i class="fa fa-phone circle-contact"></i>
                            <div class="border-skill">
                                <div>
                                <h3>Contact by phone </h3>
                                <p>+66 81-485-9666</p>
                                </div>
                            </div>
                        </div>

                    </div>
                </div>
            </div> 
            
        </div>

 
    );
  }
}

export default Contact;



