import React, { Component } from 'react';
import './about-me.css';
import Picdummy from'./img/21124035_741298699410334_1949945975_n.jpg'
import sr from '../scrollReveal.js'

class Aboutme extends Component {

    componentDidMount = () => {
        let config = { 
            duration: 2000,
            origin: 'left',
            distance: '300px',
            viewFactor:0.2
        } 
        sr.reveal('#aboutme', config)
        config = { 
            delay: 1500,
            duration: 2000,
            origin: 'left',
            distance: '300px',
            viewFactor:0.2
        } 
        sr.reveal('.position-img', config)
        config = { 
            delay: 1500,
            duration: 2000,
            origin: 'right',
            distance: '300px',
            viewFactor:0.2
        } 
        sr.reveal('.text-start', config)
      }

  render() {

    
    return (
        
        
     // <div className="welcome-landing">
     //   <h2 class="brand-heading"> Hello ,I'm Chayut </h2>
     // </div>
    //<div class="ui center aligned container segment-aboutme"><h1 class="inline">About </h1> <h1 class="inline paint-word" >Me</h1></div>
         

     <div class="center padding-end">
         <div id='aboutme' class="ui segment-aboutme bar-left"><h1 class="inline">About </h1> <h1 class="inline paint-word" >Me</h1></div>
         <div class="ui center aligned container segment-aboutme">
            <div class="ui stackable grid ">
                <div class="one wide column">
                </div>
                <div class="five wide column">
                    <img class="ui medium circular image position-img " src={Picdummy} alt="pic dummy"></img>
                </div>
                <div class="one wide column">
                </div>
                <div class="eight wide column">
                    <div class="ui piled segment text-start">
                        <h4 class="ui header"> Personal Information</h4>
                        <div class='p-personal'>
                            <p>Name : Chayut Dookandee</p>
                            <p>Nick name : Dai </p>
                            <p>Birth of day : 10 / 04 / 1994 (23 year) </p>
                        </div>
                        <h4 class="ui header">Goal</h4>
                        <p class='p-goal'>1+ Experiance in industry IT, I am aiming to be a specialist in the 
                       field of software development expertise, so that I can pass on the
                    knowledge to others.</p>
                    </div>                        
                </div>
            </div>  
         </div>
         
         
     </div>
    /*
     <div class=" header-aboutme segment-end center">
        <h1>About Me</h1>
        <div class="ui centered grid">
        <div class="six wide tablet eight wide computer column">
            <img class="ui medium circular image position-img" src={Picdummy} alt="pic dummy"></img>
         </div>
        
        </div>
       
       
     </div>
     */
    );
  }
}

export default Aboutme;



