import React, { Component } from 'react';
import './experience.css'
import sr from '../scrollReveal.js'
class Experience extends Component {

    componentDidMount = () => {
        let config = { 
            duration: 2000,
            origin: 'left',
            distance: '300px',
            viewFactor:0.2
        } 
        sr.reveal('#exphead', config)

         config = {
            delay: 1000, 
            duration: 2000,
            origin: 'bottom',
            distance: '300px',
            viewFactor:0.2
        } 
        sr.reveal('#boxjob', config, 500)

        config = {
            delay: 700, 
            duration: 2000,
            origin: 'bottom',
            distance: '300px',
            viewFactor:0.2
        } 
        sr.reveal('#timeline', config, 500)
    }

  render() {

    //<i class="fa fa-code-fork circle-skill"></i>
    /*<div class="center  padding-end">
        <div class="ui center aligned container h1skill"><h1>My Experience</h1></div>
        </div>

         <div class="item box-job">
                <div></div>
                <div class="ui raised segment ">
                <a class="ui teal right ribbon label">Reviews</a>
                    <h4 class="ui header line-ribbon">
                        <i class="plug icon"></i>
                        <div class="content">
                            Burapha University
                            <div class="sub header">2012-2016</div>
                        </div>
                    </h4>
                    
                    <p>Duties</p>
                    <p>&nbsp;&nbsp;&nbsp;- Developed web application with PHP, JSP, Android, SQL</p>
                    <p>&nbsp;&nbsp;&nbsp;- Developed Back-end Text Recognition with Java, MongoDB </p>
                    
                </div>       
            </div>
            */
    return (
        
        <div class=" padding-end">
        <div class="ui segment-aboutme bar-left" id='exphead'><h1 class="inline exp-header">My </h1> <h1 class="inline paint-word" >Experience</h1></div>

        <div class="ui middle aligned animated list ">
           
            <div class="ui middle aliged center-timeline">

                <div class="timeline" id="timeline" >

                <div class="item box-job">
                    <div></div>
                    <div class="ui raised segment" id="boxjob">
                    <a class="ui teal right ribbon label">Certificate: DocuWare Sales Advisor (DSA) </a>      
                        <h4 class="ui header line-ribbon">
                            <i class="plug icon"></i>
                            <div class="content">
                                Miracle System co, Ltd
                                <div class="sub header">Programer/Implementer</div>
                                <div class="sub header">2016-2017</div> 
                            </div>
                        </h4>
                        
                        <p>Duties</p>
                        <p>&nbsp;&nbsp;&nbsp;- Customize Document Management System (Docuware)</p>
                        <p>&nbsp;&nbsp;&nbsp;- Implement and deliver Jaymart, JMT, Jas Company </p>
                        <p>&nbsp;&nbsp;&nbsp;- Coordinated with partner in order to achieve the project goal </p> 
                    </div>       
                </div>

                <div class="item box-job">
                <div></div>
                <div class="ui raised segment " id="boxjob">
                
                    <h4 class="ui header">
                        <i class="plug icon"></i>
                        <div class="content">
                            Burapha University
                            <div class="sub header">Software Engineering</div>
                            <div class="sub header">2012-2016</div>
                        </div>
                    </h4>
                    
                    <p>Duties</p>
                    <p>&nbsp;&nbsp;&nbsp;- Developed web application with PHP, JSP, Android, SQL</p>
                    <p>&nbsp;&nbsp;&nbsp;- Developed Back-end Text Recognition with Java, MongoDB </p>
                    
                </div>       
                </div>

            
                
                </div>
            </div>    
        </div>
        
        
       
        </div>

        
             
     
    
    );
  }
}

export default Experience;



